<template>
  <div>
    <Loadings v-if="data == null"></Loadings>
    <div class="container" v-else>
      <!-- <van-nav-bar
        title="订单详情"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
      /> -->
      <div class="item">
        <div class="img" v-if="data.data.image != ''">
          <img
            :src="'http://meshua.geekcnet.com/upload/' + data.data.image"
            alt=""
          />
        </div>
        <div class="info">
          <div class="flex-sb h2">
            订单号: {{ data.data.order_no }}

            <div
              style="
                text-align: right;
                font-size: 12px;
                color: #333;
                position: relative;

                width: 30%;
              "
            >
              {{
                data.data.status == 1
                  ? "待加工"
                  : data.data.status == 2
                  ? "加工中"
                  : "已完成"
              }}
            </div>
          </div>
          <div class="flex-sa">
            <div>客户名称: {{ data.data.user_name }}</div>
            <div v-if="data.data.is_return == 1">
              回厂时间: {{ formatDate(data.data.return_time) }}
            </div>
            <div v-else>不回厂</div>
          </div>

          <div class="flex-sa">
            <div>材质名称: {{ data.data.name }}</div>
            <div>材质: {{ data.data.material }}</div>
          </div>
          <div class="flex-sa">
            <div>来料数量: {{ data.data.number }}</div>
            <div>交付数量: {{ data.data.com_number }}</div>
          </div>
          <div class="flex-sa">
            <div>来料长: {{ data.data.length }}</div>
            <div>来料宽: {{ data.data.width }}</div>
          </div>
          <div class="flex-sa">
            <div>坑纸长: {{ data.data.inch_length }}</div>
            <div>坑纸宽: {{ data.data.inch_width }}</div>
          </div>

          <div class="flex-sa">
            <div>创建日期: {{ formatDate(data.data.add_time) }}</div>
            <div>交货日期: {{ formatDate(data.data.com_time) }}</div>
          </div>
          <div>备注: {{ data.data.remark }}</div>
        </div>
      </div>
      <div class="list" v-if="data.detail.length > 0">
        <div class="caption">工艺流程</div>
        <van-collapse v-model="activeNames">
          <van-collapse-item
            v-for="(item, indexs) in data.detail"
            :key="indexs"
            :name="indexs"
            :value="
              item.status == 1
                ? `已完成:${item.com_num} (${formatDate(
                    item.completion_time
                  )}) `
                : item.status == 0
                ? '待加工'
                : '加工中'
            "
            :disabled="item.editStatus ? false : true"
          >
            <template #title>
              <div style="display: flex; align-items: center">
                <van-icon
                  @click="buchPcarts(item.poid, item.proid)"
                  v-if="item.editStatus"
                  size="16"
                  name="add-o"
                />
                {{ item.pname }}
              </div>
            </template>
            <van-cell-group v-if="role != 0">
              <div class="flex-sa">
                <van-field
                  label="来料数  "
                  placeholder="值"
                  :disabled="item.status == 1 ? true : false"
                  v-model="item.sum_num"
                  type="number"
                />
                <van-field
                  label="完成数"
                  placeholder="值"
                  :disabled="item.status == 1 ? true : false"
                  v-model="item.com_num"
                  type="number"
                  @input="culNums(item)"
                />
              </div>

              <van-field
                label="备注"
                placeholder="请输入备注内容"
                :disabled="item.status == 1 ? true : false"
                v-model="item.remark"
              />
              <!-- <van-field
                label="操作员"
                placeholder="值"
                :disabled="item.status == 1 ? true : false"
                v-model="item.completioner"
              /> -->

              <van-cell
                title="操作员"
                :value="
                  item.completioner == '' || item.completioner == 0
                    ? '点击选择操作员'
                    : item.completioner
                "
                @click="showPous(indexs)"
              />

              <van-cell v-if="item.status != 1" center title="状态">
                <template #right-icon>
                  <van-radio-group
                    v-model="item.cstatus"
                    direction="horizontal"
                  >
                    <van-radio name="0">未完成</van-radio>
                    <van-radio name="2">部分完成</van-radio>
                    <van-radio name="1">已完成</van-radio>
                  </van-radio-group>
                </template>
              </van-cell>

              <div class="btn">
                <van-button
                  v-if="item.status != 1"
                  @click="subPro(indexs)"
                  size="normal"
                  block
                  round
                  type="info"
                  >提交</van-button
                >
              </div>
            </van-cell-group>
          </van-collapse-item>
        </van-collapse>
      </div>
    </div>

    <van-popup v-model="showPou" position="bottom" :style="{ height: '50%' }">
      <van-picker
        @cancel="onCancel"
        @confirm="onConfirm"
        :loading="loading"
        title="操作员"
        show-toolbar
        :columns="columns"
      />
    </van-popup>
  </div>
</template>

<script>
import {
  setProduction,
  getPorderDetail,
  getUsers,
  buchPcart,
} from "@api/public";
import Empty from "@components/Empty";
import Loadings from "@components/Loadings";
import localStorage from "@utils/store/localStorage";
import {
  Icon,
  Field,
  Switch,
  Picker,
  Popup,
  NavBar,
  Cell,
  Toast,
  Button,
  CellGroup,
  RadioGroup,
  Radio,
  Dialog,
  Collapse,
  CollapseItem,
} from "vant";
export default {
  name: "MyMessage",
  components: {
    Empty,
    Loadings,
    [NavBar.name]: NavBar,
    [Popup.name]: Popup,
    [Dialog.name]: Dialog,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    [Picker.name]: Picker,
    [Button.name]: Button,
    [Collapse.name]: Collapse,
    [CollapseItem.name]: CollapseItem,
    [CellGroup.name]: CellGroup,
    [Icon.name]: Icon,
    [Toast.name]: Toast,
    [Switch.name]: Switch,
    [Field.name]: Field,
    [Cell.name]: Cell,
  },
  data: function () {
    return {
      columns: [],
      data: null,
      activeNames: [],
      role: 0,
      loading: true,
      showPou: false,
      nowInd: 0,
      jsonData: {},
      gyLength: 0,
      nowFish: 0,
    };
  },
  created() {
    var jsonData = JSON.parse(localStorage.get("userInfo"));
    this.role = jsonData["role"];
    this.jsonData = jsonData;
    console.log(jsonData.role_no.split(","));
    this.getData();
    //////
    if (this.role > 0) { 
      getUsers({ type: 2 })
        .then(({ data }) => {
          var arr = [];
          data.forEach((item, index) => {
            arr.push({
              text: `${item.user_login} ${item.user_nickname}`,
              defaultIndex: item.id,
            });
          });
          this.loading = false;
          this.columns = arr;
        })
        .catch((e) => {
          Toast(e.msg);
        });
    }
  },
  mounted: function () {},
  methods: {
    clickTips(e) {
      console.log(e);
      if (e) {
        Toast("请先完善上一步工艺");
      }
    },
    //限制数量

    // culNum(item) {
    //   var num = item.maxNum;
    //   item.maxNum = num > item.sum_num ? item.sum_num : num < 0 ? 0 : num;
    // },
    culNums(item) {
      var num = Number.parseInt(item.com_num) || 0;
      item.com_num = num > item.com_num ? item.com_num : num < 0 ? 0 : num;
    },
    buchPcarts(oid, pid) {
      Dialog.confirm({
        title: "分批加工",
        message: "确定要添加吗",
      })
        .then(() => {
          buchPcart({
            poid: oid,
            proid: pid,
          }).then((res) => {
            this.getData();
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    getData() {
      getPorderDetail({ id: this.$route.params.type }).then((res) => {
        var arr = [];
        var data = res.data.detail;
        var nowFish = 0;
        this.gyLength = data.length;
        data.forEach((item) => {
          item.editStatus = false;
          item.maxNum = res.data.number;
          item.status = item.cstatus;
          item.cstatus = item.cstatus + "";

          if (item.cstatus == 1) {
            nowFish++;
          }
        });

        this.nowFish = nowFish;

        for (var i = 0; i < data.length; i++) {
          data[i].checked = data[i].cstatus;

          if (i == 0) {
            data[0].editStatus = true;
          }
          if (data[i].cstatus != 0) {
            data[i].editStatus = true;

            if (i < data.length - 1) {
              data[i + 1].editStatus = true;
              data[i + 1].maxNum = data[i].com_num;
              data[i + 1].sum_num = data[i].com_num;
            }
          }
        }
        data.forEach((item) => {
          if (
            this.jsonData.role == 2 &&
            this.jsonData.role_no.indexOf(item.proid) == -1
          ) {
            item.editStatus = false;
          }
        });
        this.data = res.data;

        this.activeNames = arr;
      });
    },
    subPro: function (index) {
      var data = this.data.detail[index];

      setProduction({
        pcid: data.pcid,
        poid: this.data.data.oid,
        com: data.com_num,
        sum: data.sum_num,
        cstatus: data.cstatus,
        remark: data.remark,
        completioner: data.completioner,
        option_id: data.option_id,
        com_pocart:
          data.cstatus == 1
            ? Math.floor(((Number(this.nowFish) + 1) / this.gyLength) * 100)
            : Math.floor((this.nowFish / this.gyLength) * 100),
      }).then((res) => {
        Toast("修改成功");
        this.getData();
      });
    },
    onCancel() {
      this.showPou = false;
    },
    onConfirm(value) {
      console.log(value);
      var data = this.data;
      data["detail"][this.nowInd]["completioner"] = value.text;
      data["detail"][this.nowInd]["option_id"] = value.defaultIndex;
      this.data = data;
      this.showPou = false;
    },
    showPous(ind) {
      this.showPou = true;
      this.nowInd = ind;
    },
    formatDate: function (value) {
      let date = new Date(value * 1000);

      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;

      return y + "-" + MM + "-" + d + " ";
    },
  },
};
</script>
<style scoped>
.btn {
  margin: 20px 0;
}
.container {
  box-shadow: 0 0 5px #eee;
  background: #fff;
  border-radius: 5px;
}
.caption {
  line-height: 50px;
  text-align: center;
}
.item {
  text-align: center;
  font-size: 12px;
  padding: 10px;
  text-align: left;
  color: #666;
  line-height: 20px;
  border-bottom: 1px solid #eee;
}
.item .info div {
  width: 100%;
}
.item .h2 {
  font-size: 14px;
  color: #d2ac5b;
}
.img img {
  width: 100%;
}
</style>
